* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

html {
	font-size: 10px;
}
.App {
	text-align: center;
}

.fa-heart {
	border-radius: 50%;
	color: red;
	font-size: 70px;
}

.fa-times {
	font-size: 70px;
	color: hsl(106, 47%, 64%);
}

@media (max-width: 950px) {
	.fa-heart,
	.fa-times {
		font-size: 50px;
	}
}

@media (max-width: 680px) {
	.fa-heart,
	.fa-times {
		font-size: 50px;
	}
}
@media (max-width: 600px) {
	.fa-heart,
	.fa-times {
		font-size: 50px;
	}
}

@media (max-width: 500px) {
	.fa-heart,
	.fa-times {
		font-size: 50px;
	}
}
